import axios, {AxiosError, type AxiosRequestConfig, type AxiosResponse} from 'axios';
import {ElMessage} from 'element-plus';

// 
const re = ({data, config}: AxiosResponse) => {
  console.log('==>', data);
  if (data.code === 0) {
    return data.data;
  }
  if (data.code === -1) {
    ElMessage.error(data.message);
    console.error(
      `------ : -1--------
        path:   ${config.url}
      -------------------------- `,
    );

    return null;
  }
  if (data.code === -2) {
    ElMessage.error(data.message);
    console.error(
      `------ : -2--------
        path:   ${config.url}
      -------------------------- `,
    );
    return null;
  }
};

const instance = axios.create({
  baseURL: import.meta.env.VITE_NODE_ENV,
  timeout: 1000,
  headers: {'Content-Type': 'application/json;charset=utf-8'},
});

// 
instance.interceptors.response.use(
  function (response) {
    // 2xx 
    // data
    const {data} = response;
    // code
    const code = Number(data.code);
    data.code = Number.isNaN(code) ? -2 : code;

    if (
      typeof data.data === 'undefined' ||
      data.data === null ||
      (data.data instanceof Array && data.data.length === 0) ||
      (typeof data.data === 'object' && Object.keys(data.data).length === 0)
    ) {
      data.data = null;
    }
    data.message = data.message || data.msg || '';

    return response;
  },
  function (error) {
    //  2xx 
    ElMessage.error(`${error.request.status}: ${error.message}`);
    return Promise.reject(error);
  },
);

const post = (path: string, params: any, config?: AxiosRequestConfig & {reFull: boolean}) => {
  return instance.post(path, params, config).then((resp) => {
    console.log('==>', resp);
    if (config?.reFull) {
      return resp.data;
    }
    return re(resp);
  });
};

const http = {
  post,
};

export default http;
